*{
    margin: 0;
}

.app {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: rgba(24, 229, 239, 0.3);
}

form{
    background-color: white;
    padding: 0px 60px;
    border-radius: 10px;
}

button {
    width: 100%;
    height: 50px;
    padding: 10px;
    border: none;
    background-color:rgb(148, 17, 255);
    color: white;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 30px;
}